.loginForm__error {
   width: 250px;
   height: 50px;
   font-size: 14px;
   border: 1px solid white;
   border-top: none;
   border-radius: 0 0 10px 10px;
   color: red;
   position: absolute;
   margin-top: 64px;
   background-color: rgba(255,255,255,0.4);
   transition: .3s;
   
   display: flex;
   justify-content: center;
   align-items: center
} 